              <div ref="sharemap" className="map"></div>
              {/*<h4 className="t4"></h4>*/}
              {/*<ul className="statusWrap">*/}
                {/*{*/}
                  {/*this.state.howmuch.map((item, i) => {*/}
                    {/*return (*/}
                      {/*<li key={i} className={`statusItem ${this.state.howmuchId == item.id ? 'active' : ''}`} onClick={this.isCome.bind(this, item.id)}>{item.name}</li>*/}
                    {/*)*/}
